.blogHero {
  height: 458px;
}
@media (max-width: 1199px) {
  .blogHero {
    min-height: 504px;
  }
}
@media (max-width: 767px) {
  .blogHero {
    min-height: 537px;
  }
}
.blogHero .c {
  height: 100%;
  padding-top: 67px;
}
@media (max-width: 1199px) {
  .blogHero .c {
    align-items: flex-start;
  }
}
.blogHero__title {
  font-size: 60px;
  line-height: 1;
  display: inline-block;
  font-weight: bold;
  background-image: url("modules/blogHero/blogHero-img.jpg");
  background-size: cover;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 20px;
  margin-left: -7px;
}
@media (max-width: 1199px) {
  .blogHero__title {
    text-align: left;
    margin-left: 75px;
  }
}
@media (max-width: 767px) {
  .blogHero__title {
    text-align: center;
    margin-left: 0;
    margin-bottom: 36px;
  }
}
.blogHero__subtitle {
  font-size: 30px;
  font-weight: bold;
  color: #006dfe;
  margin-left: -5px;
}
@media (max-width: 1199px) {
  .blogHero__subtitle {
    text-align: left;
    margin-left: 77px;
  }
}
@media (max-width: 767px) {
  .blogHero__subtitle {
    text-align: center;
    margin-left: 0;
  }
}
.blog {
  background: #f4f4f4;
  padding: 49px 0 50px;
}
@media (max-width: 1199px) {
  .blog {
    padding: 23px 0 50px;
  }
}
@media (max-width: 767px) {
  .blog {
    padding: 38px 0 50px;
  }
}
.blog__tags {
  margin: 0 -6px 49px;
}
@media (max-width: 1199px) {
  .blog__tags {
    margin-bottom: 34px;
  }
}
@media (max-width: 767px) {
  .blog__tags {
    margin-bottom: 37px;
  }
}
.blog__tag {
  border: 2px solid #006dfe;
  height: 59px;
  padding: 0 22px;
  margin: 6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.1s ease-in;
}
.blog__tag:hover,
.blog__tag--active {
  background: #006dfe;
  color: #fff;
}
.blog__item {
  background: #fff;
  display: flex;
  padding: 7px;
  margin-bottom: 35px;
}
@media (max-width: 1199px) {
  .blog__item {
    flex-direction: column;
    padding: 0;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .blog__item {
    margin-bottom: 23px;
  }
}
.blog__image {
  width: 355px;
  border-bottom: 6px solid #006dfe;
  margin-bottom: -25px;
  flex-shrink: 0;
  background-size: cover;
  background-position: 50% 50%;
}
@media (max-width: 1199px) {
  .blog__image {
    width: 100%;
    height: 280px;
    margin-bottom: 0;
    border: 0;
  }
}
.blog__content {
  display: flex;
  flex-direction: column;
}
.blog__title {
  padding: 29px 45px 19px 42px;
  font-size: 30px;
  line-height: 1;
}
@media (max-width: 1199px) {
  .blog__title {
    padding: 36px 24px 19px 23px;
  }
}
@media (max-width: 767px) {
  .blog__title {
    padding: 17px 22px 13px 22px;
  }
}
.blog__title:hover {
  text-decoration: underline;
}
.blog__text {
  line-height: 1.5;
  padding: 0px 45px 52px 48px;
}
@media (max-width: 1199px) {
  .blog__text {
    padding: 0px 24px 23px 27px;
  }
}
.blog__footer {
  padding-left: 48px;
  margin-top: auto;
  border-top: 1px solid #ebebeb;
  display: flex;
  height: 43px;
  align-items: center;
}
@media (max-width: 1199px) {
  .blog__footer {
    height: 63px;
    padding-left: 25px;
  }
}
.blog__date {
  margin-right: 20px;
}
.blog__link {
  margin-left: auto;
  font-size: 18px;
  background: #fff;
  margin-top: -45px;
  padding: 0 33px 0 18px;
}
@media (max-width: 1199px) {
  .blog__link {
    margin-top: 0;
    padding-right: 27px;
  }
}
@media (max-width: 767px) {
  .blog__link {
    padding-right: 24px;
    padding-left: 0;
  }
}
.blog__link:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
  width: 7px;
  height: 7px;
  background: #006dfe;
}
.blog__link:hover {
  text-decoration: underline;
}
.blog__metriks {
  display: flex;
}
.blog__metrik {
  position: relative;
  margin-right: 25px;
  padding-left: 31px;
}
.blog__metrik:last-child {
  margin-right: 0;
}
.blog__metrik svg {
  position: absolute;
  fill: #006dfe;
  stroke: #006dfe;
  width: 20px;
  height: 20px;
  left: 14px;
  top: 50%;
  transform: translate(-50%, -50%);
}
.pagination {
  background: #fff;
  padding: 0 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .pagination {
    padding: 0 110px;
  }
}
@media (max-width: 767px) {
  .pagination {
    padding: 0 70px;
  }
}
.pagination .page-numbers {
  display: block;
  line-height: 1;
  text-align: center;
  margin: 0 14px;
  padding: 28px 3px 23px 3px;
  font-size: 18px;
  color: #2a2a2a;
  background: #fff;
  border-bottom: 3px solid transparent;
  transition: all 0.1s ease-in;
}
.pagination .page-numbers:hover,
.pagination .page-numbers.current {
  border-color: #006dfe;
  text-decoration: none;
}
.pagination .prev,
.pagination .next {
  position: absolute;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
}
.pagination .prev svg,
.pagination .next svg {
  fill: #006dfe;
  width: 50px;
  height: 14px;
  transition: all 0.1s ease-in;
}
.pagination .prev:hover,
.pagination .next:hover {
  background: #fff;
}
.pagination .prev:hover svg,
.pagination .next:hover svg {
  fill: #000;
}
.pagination .prev {
  left: 40px;
}
@media (max-width: 1199px) {
  .pagination .prev {
    left: 20px;
  }
}
@media (max-width: 767px) {
  .pagination .prev {
    left: 10px;
  }
}
.pagination .next {
  right: 40px;
}
@media (max-width: 1199px) {
  .pagination .next {
    right: 42px;
  }
}
@media (max-width: 767px) {
  .pagination .next {
    right: 10px;
  }
}
.pagination .next svg {
  transform: rotate(180deg);
}
